h1 {
  font-family: 'Enriqueta', serif;
  font-size: 45px;
}

.menu-link {
  font-family: 'Rokkitt', serif;
  
}

.header {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  /* margin-right: 15px; */
}

.nav {
  display: flex;
  flex-direction: row;
  padding-right: 10px;
  padding-top: 15px;
  margin: 0;
}



.nav-link {
  padding: 10px;
  /* background-color: black; */
  border: 2px solid black;
  text-align: right;
  /* margin: 2px 0; */
  font-size: 2vw;
  text-decoration: none;
  list-style-type: none;
  margin-right: 7px;
}

.underline {
  text-decoration: none;
  color: black;
}

.nav-link:hover {
  background-color: tomato;
  /* padding-left: 50px; */
}

.burger-icon {
  position: fixed;
  padding: 15px;
  right: 0;
  top: 0;
}

.hamburgerDropDown {
  position: fixed;
  left: 5vw;
  top: 15px;
  width: 60vw;
  margin: 0;
  padding-left: 0;
}

.ham-link {
  padding: 10px;
  list-style-type: none;
  font-size: 2em;
  background-color: white;
  border: 2px solid black;
  margin-bottom: 8px;
}

.ham-link:hover {
  background-color: tomato;
  /* padding-left: 50px; */
}









.page-container {
  /* padding-left: 5vw; */
  padding-bottom: 50px;
  
}

.p {
  margin-right: 5vw;
}

.indent {
  padding-left: 5vw;
}

.indent-more {
  margin-left: 2vw;
}



.logo {
  padding: 20px;
}
.logo-width {
  width: 25vw;
  min-width: 145px;
}


.quote {
  height: 8vw;
  font-size: 20vw;
}

.text-test {
  font-size: 1.2em;
}

.quote-text {
  font-size: 3vw;
}


